* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  body {
    font-family: sans-serif;
    margin: 20px;
    padding: 0;
  }
  
  h1 {
    margin-top: 0;
    font-size: 22px;
  }
  
  h2 {
    margin-top: 0;
    font-size: 20px;
  }
  
  h3 {
    margin-top: 0;
    font-size: 18px;
  }
  
  h4 {
    margin-top: 0;
    font-size: 16px;
  }
  
  h5 {
    margin-top: 0;
    font-size: 14px;
  }
  
  h6 {
    margin-top: 0;
    font-size: 12px;
  }
  
  code {
    font-size: 1.2em;
  }
  
  ul {
    -webkit-padding-start: 20px;
            padding-inline-start: 20px;
  }
  .square {
    background: #fff;
    border: 1px solid #e0e0e0;
    display: block;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
    -webkit-transition: background-color 0.3s ease;
    -o-transition: background-color 0.3s ease;
    transition: background-color 0.3s ease;
  }
  
  .square:hover {
    background-color: #e6e6e6;
  }
  
  .square.highlight {
    background: #e0e0e0;
    font-weight: bold;
  }
  
  .status {
    margin-bottom: 10px;
    font-weight: bold;
  }
  
  .game {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    gap: 20px;
  }
  
  .game-board {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border: 2px solid #000;
    padding: 10px;
    background-color: #f9f9f9;
  }
  
  .game-info {
    border: 2px solid #e0e0e0;
    padding: 10px;
    background-color: #f5f5f5; 
    margin-left: 20px;
  }
  
  button {
    cursor: pointer;
    padding: 10px 15px;
    border: 1px solid #e0e0e0;
    background-color: #f5f5f5;
    color: #333;
    -webkit-transition: background-color 0.3s ease;
    -o-transition: background-color 0.3s ease;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #167ab9;
  }
  


